import React, {useEffect, useState } from 'react'
import { useParams } from 'react-router-dom';
import Card from '../../../shared/components/UIElements/Card';
import ErrorModal from '../../../shared/components/UIElements/ErrorModal';
import LoadingSpinner from '../../../shared/components/UIElements/LoadingSpinner';
// import { AuthContext } from '../../../shared/context/auth-context';
import { useHttpClient } from '../../../shared/hooks/http-hook';
import Sellerproductlist from './Sellerorderlist';
import './Sellerproductlist.css'
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';


const Sellerorderitem = () => {

    const[loadedUsers, setLoadedUsers]= useState([])
    const { isLoading, error, sendRequest, clearError } = useHttpClient();
  //   const auth= useContext(AuthContext)
  // const navigate= useNavigate()
  const userId= useParams().uid;

    useEffect(() => {
        const sendrequest = async () => {
          // setIsLoading(true);       
          try {
            const responseData = await sendRequest(process.env.REACT_APP_BACKEND_URL +`/orders/productcreator/${userId}`);
      
           setLoadedUsers(responseData.orders);
            // auth.customerlogin(responseData.customer.id)
            console.log(responseData.orders)
          } catch (err) {}
        };
        sendrequest();
      }, [sendRequest, userId]);



      if (loadedUsers.length === 0) {
        return (
          <div className="center">
            <Card >
              <h2 style={{"color":"white", "textDecoration":"none"}}>No Order Found.</h2>
            </Card>
          </div>
        );
      }
    
      
  return (
    <>
        
    <ErrorModal error={error} onClear={clearError} />
        {isLoading && <LoadingSpinner asOverlay />}
        <h1 style={{justifyContent:"center", textAling:"center", display:"flex"}}>Recent Order</h1>


        <Container fluid>
      <Row style={{border:"1px solid black"}}>
        <Col style={{border:"1px solid black", fontSize:"8px", fontWeight:"bold", textAlign:"center", padding:"0"}}>DELIVER ADDRESS</Col>
        <Col style={{border:"1px solid black", fontSize:"8px", fontWeight:"bold", textAlign:"center", padding:"0"}}>TITLE</Col>
        <Col style={{border:"1px solid black", fontSize:"8px", fontWeight:"bold", textAlign:"center", padding:"0"}}>DESCRIPTION</Col>
        <Col style={{border:"1px solid black", fontSize:"8px", fontWeight:"bold", textAlign:"center", padding:"0"}}>PRICE</Col>
        <Col style={{border:"1px solid black", fontSize:"8px", fontWeight:"bold", textAlign:"center", padding:"0"}}>QUANTITY</Col>
        <Col style={{border:"1px solid black", fontSize:"8px", fontWeight:"bold", textAlign:"center", padding:"0"}}>EDIT</Col>
        <Col style={{border:"1px solid black", fontSize:"8px", fontWeight:"bold", textAlign:"center", padding:"0"}}>PURCHASE DATE</Col>
        {/* <Col style={{border:"1px solid black", fontSize:"8px", fontWeight:"bold", textAlign:"center", padding:"0"}}>IMAGE</Col> */}
      </Row>
    </Container>
        
{
  loadedUsers.map((p)=>{
    return <Sellerproductlist
    key={p.id}
    id={p.id}
    name={p.name}
      title = {p.title}
      image={p.image}
      amount={p.amount}
      description= {p.description}
      price={p.price}
      time={p.time}
      creator={p.creator}
    />
  }
  )
}

    </>
  )
}

export default Sellerorderitem
