import React, { useEffect, useState } from 'react'
import './Sellerproductlist.css'
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import { useHttpClient } from '../../../shared/hooks/http-hook'
import { NavLink } from 'react-router-dom'

const Sellerproductlist = (props) => {
  const[loadedUsers, setLoadedUsers]= useState([])
  const { sendRequest } = useHttpClient();

  // const a= Date(Date.now());
  // const b= a.toString()
  
  useEffect(() => {
      const sendrequest = async () => {
        // setIsLoading(true);       
        try {
          const responseData = await sendRequest(process.env.REACT_APP_BACKEND_URL +`/customers/${props.creator}`);
    
         setLoadedUsers(responseData.customer);
          // auth.customerlogin(responseData.customer.id)
          console.log(responseData.customer)
        } catch (err) {}
      };
      sendrequest();
    }, [sendRequest, props.creator]);
    
     
  return (
    <>

<Container fluid>
      <Row style={{border:"1px solid black"}}>
        <Col style={{border:"1px solid black", padding:"0"}}>
          <Row style={{border:"1px solid black", width:"110%"}}>
          {/* <Col style={{border:"1px solid black", fontSize:"10px", textAlign:"center"}}>Name</Col> */}
          <Col style={{border:"1px solid black", fontSize:"10px", textAlign:"center"}}>{loadedUsers.name}</Col>
          </Row>
          <Row style={{border:"1px solid black", width:"110%"}}>
          {/* <Col style={{border:"1px solid black", fontSize:"10px", textAlign:"center"}}>Email</Col> */}
          <Col style={{border:"1px solid black", fontSize:"10px", textAlign:"center"}}>{loadedUsers.email}</Col>
          </Row>
          <Row style={{border:"1px solid black", width:"110%"}}>
          {/* <Col style={{border:"1px solid black", fontSize:"10px", textAlign:"center"}}>Mobile</Col> */}
          <Col style={{border:"1px solid black", fontSize:"10px", textAlign:"center"}}>{loadedUsers.mobile}</Col>
          </Row>
        </Col>        
        <Col style={{border:"1px solid black", fontSize:"10px", textAlign:"center", padding:"0"}}>{props.title}</Col>
        <Col style={{border:"1px solid black", fontSize:"10px", textAlign:"center", padding:"0"}}>{props.description}</Col>
        <Col style={{border:"1px solid black", fontSize:"10px", textAlign:"center", padding:"0"}}>{props.price}</Col>
        <Col style={{border:"1px solid black", fontSize:"10px", textAlign:"center", padding:"0"}}>{props.amount}</Col>
        <Col style={{border:"1px solid black", fontSize:"10px", textAlign:"center", padding:"0"}}><NavLink to={`/${props.id}/Updatesellerproduct`}>✒️</NavLink></Col>
        <Col style={{border:"1px solid black", fontSize:"10px", textAlign:"center", padding:"0"}}>{props.time}</Col>
        {/* <Col style={{border:"1px solid black", fontSize:"10px", textAlign:"center", padding:"0"}}><img src={`${process.env.REACT_APP_ASSET_URL}/${props.image}`} alt="" style={{width:"50px", height:"30px"}}/></Col> */}
      </Row>
    </Container>
            
    </>
  )
}

export default Sellerproductlist
